.header {
  background-color: $color-white-smoke;

  &__inner {
    position: relative;
    padding-top: 35px;
    padding-bottom: 35px;
  }

  &__parts {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-right: -15px;
    margin-left: -15px;
  }

  &__left,
  &__right {
    flex-grow: 1;
    max-width: 50%;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
  }

  &__right {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    align-content: center;
    text-align: right;

    @include vp-1024 {
      flex-wrap: wrap;
    }
  }

  &__links {
    padding-right: 15px;
    flex-grow: 0.5;

    @include vp-1024 {
      flex-grow: 1;
      padding-right: 0;
    }
  }

  &__toggler {
    flex-shrink: 0;

    @include vp-576 {
      flex-grow: 0.5;
    }

    @include vp-1024 {
      display: none;
    }
  }

  &__main-menu {
    @include vp-1024 {
      position: absolute;
      right: 0;
      left: 0;
      bottom: 0;
      z-index: 10;
      transform: translateY(100%);
      flex-grow: 1;
      max-width: 100%;
      width: 100%;
    }
  }

  &__logo {
    color: $color-rock-spray;
  }

  &__mail {
    display: none;
    font-weight: 400;
    font-size: 18px;

    @include vp-576 {
      display: inline-flex;
    }
  }

  &__phone {
    display: none;
    margin-left: 35px;
    font-weight: 700;
    font-size: 18px;

    @include vp-576 {
      display: inline-flex;
    }

    @include vp-768 {
      font-size: 24px;
      margin-left: 80px;
    }
  }
}
